.ColorButton {
  padding: 0px;
  border-radius: 25%;
  overflow: hidden;
  display: inline-block;
  background-image: url('../../assets/16-dither.png');
  image-rendering: pixelated; /* For Chrome */
  image-rendering: -moz-crisp-edges; /* For Firefox */
  image-rendering: crisp-edges; /* For other browsers */
}

.ColorButton > span {
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 25%;
  box-sizing: border-box;
  border: #343434 solid 1px;
}

.ColorButton.Highlight > span {
  border: #e4a32a solid 3px;
}