.Workspace {
  padding: 20px;
}

.ImageRow {
  display: flex;
  flex-flow: row nowrap;
  gap: 20px;
  margin-bottom: 20px;
}

.ImageRow div {
  flex: 1 1 0px;
}

.ImageRow div input {
  width: 100%;
  min-width: none;
}

.ImageRow div button {
  min-width: none;
}

.ImageRow div input, .ImageRow div button {
  height: 2em;
  margin-bottom: 10px;
}

.ColorRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
}
