.PaletteEditor {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: stretch;
  gap: 10px;
}

.Header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}

.NameInput {
  flex: 1 1 0px;
  min-width: 0;
  font-size: 1.2em;
  font-weight: bold;
  text-align: center;
  border-width: 0px 0px 2px 0px;
}

.Header button {
  flex: 0 0 20px;
}

.EditButtonRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.ColorGrid {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: center;
  gap: 5px;
}

.ColorCell {
  flex: 0 0 2em;
  height: 1.5em;
}
